"use client";

import React, { useMemo, useState } from "react";
import Badge from "@/components/ui/badge/Badge";
import Button from "@/components/ui/button/Button";
import { Modal } from "@/components/ui/modal";
import { useModal } from "@/hooks/useModal";
import { useReservations } from "./reservationsStore";

function fmtDate(date: string) {
  // date = YYYY-MM-DD
  const [y, m, d] = date.split("-").map(Number);
  return `${String(d).padStart(2, "0")}/${String(m).padStart(2, "0")}/${y}`;
}

export default function AthleteReservations() {
  const { sessions, isReserved, remainingSpots, reserve, cancel } = useReservations();

  const now = new Date();
  const todayKey = useMemo(() => {
    // YYYY-MM-DD en horario local
    const y = now.getFullYear();
    const m = String(now.getMonth() + 1).padStart(2, "0");
    const d = String(now.getDate()).padStart(2, "0");
    return `${y}-${m}-${d}`;
  }, [now]);

  const todaySessions = useMemo(() => {
    return sessions
      .filter((s) => s.date === todayKey)
      .sort((a, b) => a.time.localeCompare(b.time));
  }, [sessions, todayKey]);

  const [pendingCancelId, setPendingCancelId] = useState<number | null>(null);
  const cancelModal = useModal();

  const openCancel = (sessionId: number) => {
    setPendingCancelId(sessionId);
    cancelModal.openModal();
  };

  const confirmCancel = () => {
    if (pendingCancelId != null) cancel(pendingCancelId);
    setPendingCancelId(null);
    cancelModal.closeModal();
  };

  const selectedToCancel =
    pendingCancelId != null ? sessions.find((s) => s.id === pendingCancelId) : null;

  return (
    <div className="space-y-5">
      {/* Header */}
      <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <div className="flex flex-col gap-2 border-b border-gray-100 px-5 py-4 dark:border-gray-800 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
              Clases de hoy
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              {fmtDate(todayKey)} · Reservá tu clase con un toque.
            </p>
          </div>

          <div className="text-sm text-gray-500 dark:text-gray-400">
            {todaySessions.length > 0 ? (
              <span>
                <span className="font-semibold text-gray-800 dark:text-white/90">
                  {todaySessions.length}
                </span>{" "}
                clases disponibles
              </span>
            ) : (
              <span>No hay clases cargadas para hoy.</span>
            )}
          </div>
        </div>
      </div>

      {/* Sessions */}
      <div className="space-y-3">
        {todaySessions.length === 0 ? (
          <div className="rounded-2xl border border-gray-200 bg-white p-6 text-center text-gray-500 dark:border-gray-800 dark:bg-white/[0.03] dark:text-gray-400">
            Hoy no hay clases disponibles.
          </div>
        ) : (
          todaySessions.map((s) => {
            const reserved = isReserved(s.id);
            const spots = remainingSpots(s.id);
            const full = spots <= 0;

            return (
              <div
                key={s.id}
                className="rounded-2xl border border-gray-200 bg-white p-5 dark:border-gray-800 dark:bg-white/[0.03]"
              >
                <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
                  <div className="space-y-1">
                    <div className="flex items-center gap-2">
                      <p className="text-sm font-semibold text-gray-800 dark:text-white/90">
                        {s.time}
                      </p>
                      <Badge
                        color={s.type === "Open" ? "warning" : "success"}
                        size="sm"
                      >
                        {s.type}
                      </Badge>
                    </div>

                    <p className="text-base font-semibold text-gray-900 dark:text-white/90">
                      {s.title}
                    </p>

                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      Coach:{" "}
                      <span className="font-medium text-gray-700 dark:text-gray-300">
                        {s.coach}
                      </span>{" "}
                      · Cupos:{" "}
                      <span className="font-medium text-gray-700 dark:text-gray-300">
                        {s.reservedCount}/{s.capacity}
                      </span>
                    </p>
                  </div>

                  <div className="flex items-center gap-3">
                    {full ? (
                      <span className="rounded-full bg-error-50 px-3 py-1 text-xs font-medium text-error-600 dark:bg-error-500/15 dark:text-error-500">
                        Completo
                      </span>
                    ) : (
                      <span className="rounded-full bg-green-50 px-3 py-1 text-xs font-medium text-green-600 dark:bg-green-500/15 dark:text-green-500">
                        Quedan {spots}
                      </span>
                    )}

                    {!reserved ? (
                      <Button
                        variant="primary"
                        disabled={full}
                        onClick={() => {
                          const res = reserve(s.id);
                          if (!res.ok) alert(res.reason);
                        }}
                      >
                        Reservar
                      </Button>
                    ) : (
                      <Button variant="outline" onClick={() => openCancel(s.id)}>
                        Cancelar
                      </Button>
                    )}
                  </div>
                </div>
              </div>
            );
          })
        )}
      </div>

      {/* Cancel Modal */}
      <Modal
        isOpen={cancelModal.isOpen}
        onClose={cancelModal.closeModal}
        className="max-w-[600px] p-5 lg:p-10"
      >
        <div className="text-center">
          <div className="relative z-1 mb-6 flex items-center justify-center">
            <svg
              className="fill-error-50 dark:fill-error-500/15"
              width="90"
              height="90"
              viewBox="0 0 90 90"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M34.364 6.85053C38.6205 -2.28351 51.3795 -2.28351 55.636 6.85053C58.0129 11.951 63.5594 14.6722 68.9556 13.3853C78.6192 11.0807 86.5743 21.2433 82.2185 30.3287C79.7862 35.402 81.1561 41.5165 85.5082 45.0122C93.3019 51.2725 90.4628 63.9451 80.7747 66.1403C75.3648 67.3661 71.5265 72.2695 71.5572 77.9156C71.6123 88.0265 60.1169 93.6664 52.3918 87.3184C48.0781 83.7737 41.9219 83.7737 37.6082 87.3184C29.8831 93.6664 18.3877 88.0266 18.4428 77.9156C18.4735 72.2695 14.6352 67.3661 9.22531 66.1403C-0.462787 63.9451 -3.30193 51.2725 4.49185 45.0122C8.84391 41.5165 10.2138 35.402 7.78151 30.3287C3.42572 21.2433 11.3808 11.0807 21.0444 13.3853C26.4406 14.6722 31.9871 11.951 34.364 6.85053Z"
                fill=""
                fillOpacity=""
              />
            </svg>
            <span className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2">
              <svg
                className="fill-error-600 dark:fill-error-500"
                width="38"
                height="38"
                viewBox="0 0 38 38"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M9.62684 11.7496C9.04105 11.1638 9.04105 10.2141 9.62684 9.6283C10.2126 9.04252 11.1624 9.04252 11.7482 9.6283L18.9985 16.8786L26.2485 9.62851C26.8343 9.04273 27.7841 9.04273 28.3699 9.62851C28.9556 10.2143 28.9556 11.164 28.3699 11.7498L21.1198 18.9999L28.3699 26.25C28.9556 26.8358 28.9556 27.7855 28.3699 28.3713C27.7841 28.9571 26.8343 28.9571 26.2485 28.3713L18.9985 21.1212L11.7482 28.3715C11.1624 28.9573 10.2126 28.9573 9.62684 28.3715C9.04105 27.7857 9.04105 26.836 9.62684 26.2502L16.8771 18.9999L9.62684 11.7496Z"
                  fill=""
                />
              </svg>
            </span>
          </div>

          <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90 sm:text-title-sm">
            ¿Cancelar reserva?
          </h4>

          <p className="text-sm leading-6 text-gray-500 dark:text-gray-400">
            {selectedToCancel ? (
              <>
                Vas a cancelar{" "}
                <span className="font-semibold">{selectedToCancel.title}</span>{" "}
                ({selectedToCancel.time}).
              </>
            ) : (
              "Confirmá si querés cancelar esta reserva."
            )}
          </p>

          <div className="mt-7 flex w-full items-center justify-center gap-3">
            <button
              type="button"
              onClick={cancelModal.closeModal}
              className="flex justify-center rounded-lg border border-gray-300 bg-white px-4 py-3 text-sm font-medium text-gray-700 shadow-theme-xs hover:bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300 dark:hover:bg-white/[0.03]"
            >
              Volver
            </button>
            <button
              type="button"
              onClick={confirmCancel}
              className="flex justify-center rounded-lg bg-error-500 px-4 py-3 text-sm font-medium text-white shadow-theme-xs hover:bg-error-600"
            >
              Sí, cancelar
            </button>
          </div>
        </div>
      </Modal>
    </div>
  );
}
